<template>

	<view class="content">
		<topbar bg_color="#FFFFFF" text_color="#fff" :have_top="true">
			<text class="icon-fanhui iconfont" style="color: #999999;" @click="fanhui"> </text>
			<text class=" " style="color: #FFFFFF;"></text>
			<text class="pr5 fs14" style="color: #FFFFFF;"> </text>
		</topbar>
		<view class="pl30 pt25 fs20 fwb">
			忘记密码
		</view>
		<view class="pl30 pt35 pr30 mt12 ">
			<view class="flex b-b-1 pb5">
				<text class="icon-yonghu iconfont c2"></text>
				<!-- <input  class="pupu_shouj" type="text" placeholder="用户名/手机号" placeholder-class="c2 pl10 fs16"/> -->
				<input placeholder-class="c2 fs16 pupu_shouj" type="text" placeholder="用户名/手机号"
					class="pupu_shouj c2 pl10 fs16" />
			</view>

			<view class="flex b-b-1 pt30 pb5">
				<text class="icon-mima1 iconfont c2 fwb"></text>
				<input placeholder-class="c2  fs16 " type="text" class="pupu_shoujs c2 pl10 fs16" placeholder="验证码" />
				<button @click='codeClick' v-if='changess' class="fs12 bgcf" style="color: #FF1919;"> 获取验证码</button>
				<button @click='codeClick' :disabled='disabled' v-else class="fs12 "
					style="background-color: #ffffff;">{{second}}后重试</button>
			</view>
			<view class="flex b-b-1 pt30 pb5">
				<text class="icon-mima1 iconfont c2 fwb"></text>

				<!-- <input  class="pupu_shouj" :type="seen ? type_text : type_password" placeholder="密码" placeholder-class="c2 pl10 fs16 pupu_shouj" placeholder-style="width: 200rpx;"/> -->
				<input placeholder-class="c2 fs16 pupu_shouj " type="password" class="pupu_shouj c2 pl10 fs16"
					placeholder="新密码" v-model="password"/>

			</view>
			<view class="flex b-b-1 pt30 pb5">
				<text class="icon-mima1 iconfont c2 fwb"></text>

				<!-- <input  class="pupu_shouj" :type="seen ? type_text : type_password" placeholder="密码" placeholder-class="c2 pl10 fs16 pupu_shouj" placeholder-style="width: 200rpx;"/> -->
				<input placeholder-class="c2 fs16 pupu_shouj " type="password" class="pupu_shouj c2 pl10 fs16"
					placeholder="重复新密码" v-model="repassword"/>

			</view>
		</view>
		<view class="funs flex aic ju_c cf " @click="qued">
			确定
		</view>

	</view>
</template>

<script>
	import api from '../../components/service.js';
	var that
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		name: "login",
		data() {
			return {
				showText: true,
				second: 59,
				disabled: false,
				changess: true,
				usr_pwd: "",
				seen: false,
				type_text: "text",
				password: "",
				repassword:'',
			};
		},
		onHide() {

		},
		onLoad() {
			that = this
		},
		onShow() {

		},

		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName', 'userinfo'])
		},
		methods: {
			...mapMutations(['logout', 'login']),
			getimg(img) {
				return api.getimg(img)
			},
			jump(e) {
				return api.jump(e)
			},
			fanhui() {
				wx.navigateBack({
					delta: 1
				})
			},
			changeSeen() {
				this.seen = !this.seen;
			},
			qued() {
				if(that.password!==that.repassword) {
					uni.showToast({
						icon: 'none',
						title: '两次密码输入不一致'
					})
					return
				}
				wx.navigateBack({
					delta: 1
				})
			},
			// 获取验证码
			codeClick() {

				that.changess = false;
				that.disabled = true
				// console.log("1")
				that.showText = false
				var interval = setInterval(() => {
					--that.second
				}, 1000)

				setTimeout(() => {
					clearInterval(interval)
					that.showText = true
					that.second = 60
					that.disabled = false
					that.changess = true;
				}, 60000)


			},
		}
	}
</script>

<style>
	.c2 {
		color: #c2c2c2;
	}

	page {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.content {
		background-color: #fff;
		width: 100%;
		height: 100%;
		margin: 0;
		padding: 0;
	}

	.funs {
		width: 600rpx;
		height: 90rpx;
		background: #FF1919;
		border-radius: 45rpx;
		margin-left: 70rpx;
		margin-top: 120rpx;
	}
</style>
